import { ChangeDetectionStrategy, ChangeDetectorRef, Component } from "@angular/core";

import { App, NavController, NavParams, Platform, Tabs } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsCartService } from "@services/cart.service";
import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";


import { Backend } from "@backend/index";
import { OrderByPipe } from "@pipes/order-by";
import { PaginationInstance } from "ngx-pagination";


@Component({
    selector: "page-shopwindow-items",
    templateUrl: "shopwindow-items.html"
})
export class ShopwindowItemsPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    items: Array<any>;
    showItems: boolean;
    category: string;
    categoryInfo: any;
    cart: Array<any>;
    currentPageItemIds: Array<any>;
    cartInfo: any;
    inventory: Array<any>;
    promotion: Array<any>;
    inventoryOptions: any;
    promotionOptions: any;
    emptyList: boolean;
    merchant: string;
    merchantUser: string;
    orderBy: OrderByPipe;
    cartOptions = {
        showPicture: true,
        showDescription: true,
        showPrice: true,
        showPriceLabel: true,
        showDiscountPrice: true
    };
    pagination: PaginationInstance = {
        itemsPerPage: 10,
        currentPage: 1
    };

    constructor(private platform: Platform,
        private app: App,
        private navCtrl: NavController,
        private navParams: NavParams,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private globals: AppGlobals,
        private cartService: MarsCartService,
        private authService: MarsAuthService,
        private interactionService: MarsInteractionService) {
        this.orderBy = new OrderByPipe();
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    };

    ionViewWillEnter() {
        this.initialize();
    };

    private async initialize() {
        this.category = this.navParams.get("category");
        this.merchantUser = this.navParams.get("merchant");
        await this.getMerchant();
        this.getMarketItems();
        this.changeDetector.detectChanges();
        this.cartService.updated.subscribe(($event) => {
            this.cartInfo = $event.cart;
            this.cart = $event.cart.items;
            this.filterLists();
            this.changeDetector.detectChanges();
        });
    }

    private async getMerchant() {
        try {
            let merchant = (await Backend.getMerchantByUsername({ username: this.merchantUser })).data;
            this.merchant = merchant._id;
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    async getMarketItems() {
        try {
            this.categoryInfo = (await Backend.getInventoryItems({
                merchant: this.merchant,
                category: this.category,
                pageSize: this.pagination.itemsPerPage,
                page: this.pagination.currentPage - 1,
            })).data;
            this.promotion = (await Backend.getPromotionItems({ id: "active", merchant: this.merchant, category: this.category })).data.items;
            this.inventory = this.categoryInfo.items;
            this.pagination.totalItems = this.categoryInfo.totalCount;
            this.cartInfo = await this.cartService.load();
            this.cart = this.cartInfo.items;
            this.filterLists();
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    };

    filterLists() {
        this.cart = this.filterCart(this.cart, this.categoryInfo.id);
        this.emptyList = !((this.inventory.length > 0) && (this.promotion.length > 0) && (this.cart.length > 0));
        let filteredInventory = this.inventory.filter((item) => this.isNotOnList(item, this.promotion) && this.isNotOnList(item, this.cart));
        let filteredPromotion = this.promotion.filter((item) => this.isNotOnList(item, this.cart));
        let orderedItems = this.orderBy.order(filteredInventory.concat(this.cart), "information.name");
        this.items = filteredPromotion.concat(orderedItems);
        this.showItems = true;
        this.changeDetector.detectChanges();
    };

    filterCart(cart: Array<any>, categoryId: string) {
        return cart.filter((item) => { // Show only the items from the current inventory category
            return item && item.inventoryRef && this.inventory.indexOfObject("_id", item.inventoryRef._id) > -1;
        });
    }

    isNotOnList(item, list) {
        let listItemId = "";
        let itemId = item && item.information && item.information._id ? item.information._id : "";
        for (let index in list) {
            let item = list[index];
            listItemId = item && item.information && item.information._id ? item.information._id : "";
            if (itemId == listItemId) return false;
        }
        return true;
    };

    view(item) {
        this.navigationService.goTo("MarketItemDetailsPage", {
            slug: item.information.slug
        });
    };

    goToCartPage() {
        let tabs = (this.navCtrl.parent as Tabs);
        tabs._tabs.map((tab, index) => {
            if (tab.root == "CartPage") tabs.select(tab);
        });
    };

    requestNewItem() {
        this.navigationService.goTo('MarketItemInformationPage', {
            category: this.category,
        });
    }
}



